<template>
  <div class="goods-info">
    <!----->
    <transition
      @before-enter="beforeEnter"
      @enter="enter"
      @after-enter="afterEnter">
      <div class="ball" v-show="ballFlag"></div>
    </transition>
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <!---->
          <swiper :lunbolist="lunbolist" :isfull="false"></swiper>
        </div>
      </div>
    </div>
    <div class="mui-card">
      <div class="mui-card-header">8 </div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p class="price">
            <del>2999.00</del>&nbsp;&nbsp;<span class="now">{{price}}</span>
          </p>
          <p>
            
            <numberbox ref="numbox" :max="maxCount"></numberbox>
          </p>
          <p>
            <mt-button type="primary" size="small"></mt-button>
            <mt-button type="danger" size="small" @click="addCart()"></mt-button>
          </p>
        </div>
      </div>
    </div>
    <div class="mui-card">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>7437788</p>
          <p>{{maxCount}}</p>
          <p>2018-09-13 14:33:02</p>
        </div>
      </div>
      <div class="mui-card-footer">
        <mt-button type="primary" size="large" plain @click="toDesc(1)"></mt-button>
        <mt-button type="danger" size="large" plain @click="toComment(1)"></mt-button>
      </div>
    </div>

  </div>
</template>

<script>
  import swiper from '../sub/Swiper'
  import { Toast } from 'mint-ui'
  import numberbox from '../sub/GoodsInfoNumberBox'

  export default {
    name: "goods-info",
    data(){
      return{
        lunbolist:[],
        ballFlag: false, //
        maxCount: 60, // 
        id: this.$route.params.id,
        price: '2899.00'
      }
    },
    created(){
      this.getData();
    },
    methods:{
      getData() {
        this.$http.get("random/data/%E7%A6%8F%E5%88%A9/5").then(response => {
          if (!response.body.error) {
            this.lunbolist = response.body.results
          } else {
            Toast('')
          }
        }).catch(error => {
          console.log(error)
        })
      },
      toDesc(id){
        this.$router.push({name: 'goodsdesc',params:{id}})
      },
      toComment(id){
        this.$router.push({name: 'goodscomment',params:{id}})
      },
      addCart(){
        //
        this.ballFlag = !this.ballFlag;
        console.log(this.$refs.numbox.getVal())

        // {id: id, count: , price: , selected: false }
        var goodsinfo = {
          id: this.id,
          count: this.$refs.numbox.getVal(),
          price: this.price,
          selected: true
        }
        this.$store.commit('addToCart', goodsinfo)
        console.log(this.$store.state.cart)
      },
      beforeEnter(el){
        //
        el.style.transform = "translate(0px,0px)"
      },
      enter(el,done){
        el.offsetWidth; //
        el.style.transform = "translate(77px,227px)";
        el.style.transition = "all 1s ease";
        done()  //done afterEnter()
      },
      afterEnter(el){
        //
        //
        this.ballFlag = !this.ballFlag;
      },
    },
    components:{
      swiper,
      numberbox
    },


  }
</script>

<style lang="scss" scoped>
  .goods-info{
    position: relative;
    .ball{
      width: 15px;
      height: 15px;
      border-radius: 50%;
      background-color: red;
      z-index: 99;
      position: absolute;
      top: 340px;
      left: 151px;
    }

    .price{

      .now{
        color: red;
      }
    }
    .mui-card-footer{
      display: block;
      button{
        margin: 15px 0;
      }
    }
  }

</style>



