import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import storage from '../model/storage'

//  main.js storemain
//  state
var cart = storage.get('cart');

const store = new Vuex.Store({
  state: {
    //  data
    //  this.$store.state.count 
    count: 0,
    // 
    // {id: id, count: , price: , selected: false }
    cart: cart
  },
  mutations: {
    // storestatemutations
    // mutationsthis.$store.commit('')
    //  mutations this.$emit('')
    getCount(state, count){
      state.count = count;
    },
    addToCart(state, goodsinfo){  //
      // 1. 
      // 2. pushcart
      //  
      var flag = false
      state.cart.some(item =>{  // some - 
        if (item.id == goodsinfo.id) {
          item.count += parseInt(goodsinfo.count)
          flag = true //true
          return true
        }
      })
      //  flagfalsepush
      if (!flag){
        state.cart.push(goodsinfo)
      }
      // cartcartlocalstorage
      storage.set("cart",state.cart)
    },
    updateCart(state, goodsinfo){ //
      state.cart.some(item =>{
        if (item.id == goodsinfo.id){
          item.count = goodsinfo.count
          return true
        }
      })
      // 
      storage.set("cart",state.cart)
    },
    delGoods(state, id){ //
      state.cart.some((item,index,arr) =>{
        if (item.id == id){
          arr.splice(index, 1);
          return true
        }
      })
      // cartcartlocalstorage
      storage.set("cart",state.cart)
    },
    selectedGoods(state, id){ //switch 
      state.cart.some((item) =>{
        if (item.id == id){
          item.selected = !item.selected
          return true
        }
      })
      // cartcartlocalstorage
      storage.set("cart",state.cart)
    }
  },
  getters:{
    getAllCount(state){
      var c = 0
      state.cart.forEach(item =>{
        c+= parseInt(item.count)
      })
      return c
    },
    getGoodsCount(state){
      //  id: count id
      var o = {}
      state.cart.forEach(item=>{
        o[item.id] = item.count
      })
      return o;
    },
    getGoodsSelected(state){
      //  id: boolean id
      var o = {}
      state.cart.forEach(item=>{
        o[item.id] = item.selected
      })
      return o;
    },
    getAllSelectedGoodsCountAndPrice(state){
      var o = {
        count: 0, // 
        price: 0  // 
      }
      state.cart.forEach(item =>{
        if (item.selected){
          o.count += parseInt(item.count);
          o.price += parseFloat(parseInt(item.count) * parseFloat(item.price))
        }
      })
      return o
    },
  }
})

export default store



